import { useState, useCallback } from 'react'

const locales = {
  _: '??',
  en: 'English',
  de: 'Deutsch',
  es: 'Espaol',
  pt: 'Portugus',
  fr: 'Franais',
  it: 'Italiano',
  nl: 'Nederlands', // Dutch
  da: 'Dansk', // Danish
  sv: 'Svenska', // Swedish
  nb: 'Norsk bokml', // Norwegian
  fi: 'Suomi', // Finish
  mt: 'Malti', // Maltese
  pl: 'Jzyk polski', // Polish
  ru: ' ', // Russian
  bg: ' ', // Bulgarian
  tr: 'Trke',
  ar: '', // Arabic
  el: '', // Greek
  ro: 'limba romn', // Romanian
  sl: 'slovenina', // Slovenian
  // uk: ' ', // Ukrainian
  // cy: 'Cymraeg', // Welsh
}

const localesArray = Object.entries(locales)
.map(([code, nativeName]) => ({code, nativeName}))

const defaultOptions = Object.keys(locales)

function LocaleSelect({
  defaultValue,
  onChange,
  options = defaultOptions,
}) {
  if (
    defaultValue === null
    || defaultValue === undefined
    || defaultValue === ''
    || locales.hasOwnProperty(defaultValue) === false
  ) {
    defaultValue = '_'
  }

  const [changedLocale, setChangedLocale] = useState(defaultValue)

  const handleLocaleChange = useCallback((event) => {
    let newLocale = event.target.value
    setChangedLocale(newLocale)
    
    if (newLocale === '_') {
      newLocale = null
    }

    if (onChange) {
      onChange(newLocale)
    }
  }, [ setChangedLocale, onChange ])
  
  return (
    <div
      className="wrapped_select"
      placeholder={changedLocale === '_' ? locales._ : changedLocale.toUpperCase()}
      style={{
        margin: '0 var(--basis) 0 0',
        flexShrink: 0,
      }}
    >
      <select
        onChange={handleLocaleChange}
        defaultValue={defaultValue}
      >
        {
          localesArray
          .filter(({ code }) => options.includes(code))
          .map(({ code, nativeName }) => <option key={code} value={code}>{code === '_' ? nativeName : `${code.toUpperCase()}  ${nativeName}`}</option>)
        }
      </select>
    </div>
  )
}

export default LocaleSelect
